<template>
  <div class="purchased-container">
    <div
      class="no-thing f-col-c"
      v-if="myBought.list.length === 0 && vipFree.list.length === 0"
    >
      <img src="@/assets/images/no_buy.png" alt="">
      <p>
        还没有买过相关内容噢～
      </p>
      <div @click="toMorningParentsPage" class="f-c">
        继续逛逛
      </div>
    </div>
    
    <div v-else>
      <PurchasedTab
        style="background-color: #fff"
        v-if="userInfo && userInfo.isMember"
        :tags="tags"
        :activeTab="activeTabIndex"
        @clickTab="clickTab"
      />

      <!-- 我的已购 -->
      <div
        class="album-box"
        v-if="activeTabIndex === 0 && myBought.list.length > 0"
      >
        <van-list
          v-model="myBought.loading"
          :finished="myBought.finished"
          finished-text="没有更多了"
          @load="getMyBoughtList"
        >
          <div
            @click="clickAlbum(album)"
            v-for="album in myBought.list"
            :key="album._id"
          >
            <div class="content-area">
              <BaseListLayout
                :album="album"
                :showPayStatus="false"
              />
            </div>
            <div class="split-line-1" />
          </div>
        </van-list>
      </div>

      <!-- 会员畅听 -->
      <div
        class="album-box"
        v-else-if="activeTabIndex === 1 && vipFree.list.length > 0"
      >
        <van-list
          v-model="vipFree.loading"
          :finished="vipFree.finished"
          finished-text="没有更多了"
          @load="getVipFreeAlbum"
        >
          <div
            @click="clickAlbum(album)"
            v-for="album in vipFree.list"
            :key="album._id"
          >
            <div class="content-area">
              <BaseListLayout
                :album="album"
                :showPayStatus="false"
              />
            </div>
            <div class="split-line-1" />
          </div>
        </van-list>
      </div>
    </div>

    <div class="bottom-block-135" />

    <Tabbar class="tabbar-fixed-bottom" />
  </div>
</template>

<script>
import BaseListLayout from '@/components/homePageLayout/BaseListLayout.vue';
import Tabbar from '@/components/tabbar/Tabbar.vue';
import PurchasedTab from '@/components/purchased/PurchasedTab.vue';
import { apiAlbumBuylist } from '@/service/api/program';
import { apiAlbumListByChargetype } from '@/service/api/program';
import { toAlbumPage } from '@/utils/proxyRouteSkip';
import { db } from '@/utils/lowdb';
import Vue from 'vue';
import { List } from 'vant';

Vue.use(List);

export default {
  components: {
    Tabbar,
    PurchasedTab,
    BaseListLayout
  },

  data() {
    return {
      activeTabIndex: 0, // ? 0: 我的已购 1: 会员畅听
      tags: ['我的已购', '会员畅听'],


      myBought: {
        list: [],
        finished: true,
        pageSize: 20,
        pageNum: 0,
        loading: false
      },

      vipFree: {
        list: [],
        finished: true,
        pageSize: 20,
        pageNum: 0,
        loading: false
      },

      userInfo: null
    }
  },

  created() {
    document.title = '已购';
  },

  mounted() {
    this.getLocalUserInfo();
    this.getMyBoughtList();
  },

  methods: {
    // todo 获取本地缓存中的用户信息
    getLocalUserInfo() {
      this.userInfo = db.get('userInfo').value();
      if (this.userInfo.isMember) {
        this.checkQueryHasActiveIndex();
        this.getVipFreeAlbum();
      }
    },

    // todo 检测 query 中是否有参数
    checkQueryHasActiveIndex() {
      if (!this.$route.query.hasOwnProperty('activeIndex')) return ;
      let activeIndex = this.$route.query.activeIndex;
      activeIndex = (+ activeIndex);
      if (![0, 1].includes(activeIndex)) return ;
      this.activeTabIndex = activeIndex;
    },

    // todo 跳转到早安父母页面
    toMorningParentsPage() {
      this.$router.push({
        name: 'morning-parents'
      })
    },

    // todo 获取会员免费的专辑
    async getVipFreeAlbum() {
      const vipFreeAlbumResponse = await apiAlbumListByChargetype('1', this.vipFree.pageNum, this.vipFree.pageSize);
      if (!vipFreeAlbumResponse || vipFreeAlbumResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.vipFree.list = [...this.vipFree.list, ...vipFreeAlbumResponse.data];
      if (vipFreeAlbumResponse.data.length < this.vipFree.pageSize) {
        this.vipFree.finished = true;
      } else {
        this.vipFree.finished = false;
        this.vipFree.pageNum += 1;
      }
      this.vipFree.loading = false;
    },

    // todo 切换 tab 对应的内容显示
    clickTab(index) {
      this.activeTabIndex = index;
    },

    // todo 获取我已购买的专辑
    async getMyBoughtList() {
      const myBoughtResponse = await apiAlbumBuylist(this.myBought.pageNum, this.myBought.pageSize);
      if (!myBoughtResponse || myBoughtResponse.type === 'fail') return Toast('服务器错误，请联系客服');
      this.myBought.list = [...this.myBought.list, ...myBoughtResponse.data];
      if (myBoughtResponse.data.length < this.myBought.pageSize) {
        this.myBought.finished = true;
      } else {
        this.myBought.finished = false;
        this.myBought.pageNum += 1;
      }
      this.myBought.loading = false;
    },

    clickAlbum(album) {
      toAlbumPage(album.chargeType, album.isBuy, album._id);
    }
  }
}
</script>

<style lang="scss" scoped>
.purchased-container {
  width: 100%;
  min-height: 100%;

  .album-box {
    width: 100%;
    padding: 0 16px;
    box-sizing: border-box;
    background-color: #fff;

    .content-area {
      padding: 20px 0;
    }
  }

  .split-line-1 {
    width: 343px;
    height: 1px;
    background: rgba(233,233,233,1);
  }

  .no-thing {
    width: 100%;
    height: 100%;

    img {
      width: 150px;
      height: 150px;
    }
    p {
      margin-top: 10px;
      font-size: 14px;
      color: rgba(153,153,153,1);
    }
    div {
      margin-top: 25px;
      width: 120px;
      height: 40px;
      background: rgba(244,186,65,1);
      border-radius: 20px;
      font-size: 16px;
      color: rgba(255,255,255,1);
    }
  }
}
</style>


