<template>
  <div class="purchased-tab-tab-container">

    <div class="tabs-box">
      <div
        v-for="(item, index) in tags"
        :key="index"
        @click="clickTab(index)"
        :class="['tab', activeTab === index ? 'active' : '']"
      >
        {{ item }}
        <div v-show="activeTab === index" class="bar" />
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { Tab, Tabs } from 'vant';

Vue.use(Tab).use(Tabs);
export default {
  props: {
    tags: {
      type: Array,
      default: ['分类1', '分类2']
    },
    activeTab: {
      type: Number,
      default: 0
    }
  },

  data() {
    return {
      
    }
  },

  methods: {
    clickTab(index) {
      this.$emit( 'clickTab', index );
    }
  }
}
</script>

<style lang="scss" scoped>
.purchased-tab-tab-container {
  width: 100%;
  height: 45px;
  border-bottom: #EEEEEE 1px solid;

  .tabs-box {
    width: 100%;
    height: 45px;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;

    .tab {
      width: 50%;
      display: inline-block;
      height: 45px;
      line-height: 45px;
      text-align: center;
      color: #666666;
      padding: 0 10px;
      font-size: 14px;
      color: rgba(102,102,102,1);

      .bar {
        width: 30px;
        height: 3px;
        background: rgba(244,186,65,1);
        border-radius: 1px;
        position: relative;
        top: -3px;
        left: calc(50% - 15px);
      }
    }
    .active {
      font-weight: bold;
      color: rgba(244,186,65,1);
      font-size: 16px;
    }
  }
  .tabs-box::-webkit-scrollbar {
    display: none;
  }
}
</style>


